import { useCallback, useEffect, useMemo, useState } from "react";

export const usePersistentState = <T>(key: string, defaultValue: T) => {
  // Have to do this to prevent default values that are empty arrays and the
  // likes from updating the state all the time.
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const memoDefaultValue = useMemo(() => defaultValue, []);
  const raw = localStorage.getItem(key);
  const initialState = raw ? JSON.parse(raw) : memoDefaultValue;
  const [state, setState] = useState<T>(initialState);
  useEffect(
    function readOnMount() {
      const raw = localStorage.getItem(key);
      if (raw) setState(JSON.parse(raw));
      else setState(memoDefaultValue);
    },
    [memoDefaultValue, key]
  );
  const setStateExternal = useCallback(
    (value: T) => {
      setState(value);
      localStorage.setItem(key, JSON.stringify(value));
    },
    [key]
  );
  return [state, setStateExternal] as const;
};
