import { compact, partial } from "lodash";
import { useContext, useEffect } from "react";
import useSWR, { useSWRInfinite } from "swr";
import { FdcApiContext } from "../../contexts/fdcApiKey";
import { foodResultToHealthyFood } from "../models";
import { FDAErrorResponse, FoodResult } from "./types";

const getKey = (API_KEY?: string, id?: number) => {
  if (!API_KEY) return null;
  if (!id) return null;
  return `https://api.nal.usda.gov/fdc/v1/food/${id}?api_key=${API_KEY}`;
};

const getFoodOrUndefined = (data?: FoodResult | FDAErrorResponse) => {
  if (!data) return undefined;
  if (!("fdcId" in data)) return undefined;
  return data;
};

const getErrorOrUndefined = (data?: FoodResult | FDAErrorResponse) => {
  if (!data) return undefined;
  if ("fdcId" in data) return undefined;
  return data;
};

export const useFood = (id?: number) => {
  const { key: API_KEY } = useContext(FdcApiContext);
  const key = getKey(API_KEY, id);
  const { data, error } = useSWR<FoodResult | FDAErrorResponse>(key);
  const food = getFoodOrUndefined(data);
  const healthyFood = food && foodResultToHealthyFood(food);
  const apiError = getErrorOrUndefined(data);
  const isLoading = key && !error && !data;
  return { food: healthyFood, data, apiError, error, isLoading };
};

export const useFoods = (ids: number[]) => {
  const { key: API_KEY } = useContext(FdcApiContext);
  const keys = ids.map(partial(getKey, API_KEY));
  const loadKey = (index: number) => keys[index];
  const { data: responses, error, setSize } = useSWRInfinite<
    FoodResult | FDAErrorResponse
  >(loadKey);
  useEffect(
    function updateSize() {
      setSize(ids.length);
    },
    [setSize, ids.length]
  );
  const foods = compact(responses?.map(getFoodOrUndefined)).map(
    foodResultToHealthyFood
  );
  const errors = compact(responses?.map(getErrorOrUndefined));
  return { foods, responses, errors, error };
};
