import { find, first, groupBy, map } from "lodash";
import React, { HTMLAttributes } from "react";
import styled from "styled-components";
import { useFood, useFoods } from "../data/fdc/food";
import { Meal as IMeal, Morsel as IMorsel, nutrients } from "../data/models";

interface EatenFoodProps extends HTMLAttributes<HTMLElement> {
  morsel: IMorsel;
}

const Morsel: React.FC<EatenFoodProps> = ({ morsel, ...props }) => {
  const { food, error } = useFood(morsel.id);

  return (
    <div {...props}>
      {error && error.toString()}
      {!food && !error && "loading..."}
      {food && `${morsel.amount}${morsel.unit} ${food?.name} (${food?.brand})`}
    </div>
  );
};

export const MorselList = styled.ul`
  margin: 0;
  padding: 0;
  li {
    list-style: none;
  }
`;

export interface Props extends HTMLAttributes<HTMLElement> {
  meal: IMeal;
}

export const Meal: React.FC<Props> = ({ meal, ...props }) => {
  const { foods } = useFoods(map(meal.foods, "id"));
  if (!foods) return null;
  const accumulatedNutrients = nutrients.map((nutrient) => ({
    name: nutrient.fdcName,
    value: foods.reduce(
      (sum, food) =>
        sum + (find(food.nutrients, { name: nutrient.fdcName })?.value ?? 0),
      0
    ),
    unit: find(first(foods)?.nutrients, { name: nutrient.fdcName })?.unit,
    group: nutrient.group,
  }));
  const groups = groupBy(accumulatedNutrients, "group");
  return (
    <div {...props}>
      <h3>{meal.date}</h3>
      <MorselList>
        {meal.foods.map((food) => (
          <li key={food.id}>
            <Morsel morsel={food} />
          </li>
        ))}
      </MorselList>
      <table>
        <thead>
          <tr>
            <th>Nutrient</th>
            <th>Amount</th>
          </tr>
        </thead>
        <tbody>
          {Object.keys(groups).map((groupKey) => (
            <React.Fragment key={groupKey}>
              <h4>{groupKey}</h4>
              {groups[groupKey].map((nutrient) => (
                <tr>
                  <td>{nutrient.name}</td>
                  <td>
                    {Math.round(nutrient.value)} {nutrient.unit}
                  </td>
                </tr>
              ))}
            </React.Fragment>
          ))}
        </tbody>
      </table>
    </div>
  );
};
